<template>
  <div class="member-box">
      <div class="query-wrapper">
        <div>
          <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
            <el-form-item label="客户手机号" prop="phone">
              <el-input v-model="queryForm.phone" :placeholder="'input' | solvePlaceholder" />
            </el-form-item>
            <el-form-item label="录入人" prop="flowerName">
              <el-input v-model="queryForm.flowerName" :placeholder="'input' | solvePlaceholder" />
            </el-form-item>
            <el-form-item label="客户姓名" prop="customerName">
              <el-input v-model="queryForm.customerName" :placeholder="'input' | solvePlaceholder" />
            </el-form-item>
          </el-form>
        </div>
        <div class="query-btn-group mt15">
          <el-button type="primary" @click="queryContent('query')">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
      <div class="table-box">
        <el-row class="table-header" type="flex" justify="space-between">
          <el-col :span="6" class="customer-num" v-if="selectedData.length">共选择了{{selectedData.length}}个客户</el-col>
          <el-col :span="18" :offset="selectedData.length?0:6" class="table-header-btn-group">
            <el-button type="primary" @click="queryContent('query', '', '0')">全部</el-button>
            <el-button type="primary" @click="queryContent('query', '', '1')">今日新增</el-button>
            <el-button class="mr15" type="primary" @click="queryContent('query', '', '2')">从未跟进</el-button>
            <el-dropdown class="dropdown mr15" v-for="(item, index) in tableHeaderBtnGroups" :key="item | solveKey">
              <el-button type="primary">{{item.title}}</el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="(cItem) in item.children">
                  <span style="display:block;" @click="opera(item.type, cItem)">{{item.title}}{{cItem.title}}</span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
        <el-table
          class="w100"
          ref="customerTable"
          :data="customers"
          tooltip-effect="dark"
          @selection-change="val => selectedData = val"
        >
          <el-table-column type="selection" width="55" />
          <el-table-column  label="客户名称">
            <template slot-scope="scope">
              <span @click="lookRelaRems(scope.row)" class="cur hover-active" style="text-decoration: underline;">{{scope.row.customerName}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="phone" label="手机号" />
          <el-table-column prop="preAmount" label="意向额度(万元)" />
          <el-table-column prop="city" label="所在城市" >
            <template slot-scope="scope">{{scope.row.province}}{{scope.row.city}}</template>
          </el-table-column>
          <el-table-column label="客户状态" >
            <template slot-scope="scope">{{scope.row.status | solveCustomerStatus}}</template>
          </el-table-column>
          <el-table-column label="最近跟进时间" >
            <template slot-scope="scope">{{scope.row.flowerTime | solveTime}}</template>
          </el-table-column>
          <el-table-column  label="操作" >
            <template slot-scope="scope">
              <el-button type="primary" @click="addFollowRember(scope.row)">写跟进</el-button>
            </template>
          </el-table-column>
        </el-table>

        <MemberPagination @callback="queryContent" :total="total" :pages="pages"/>
      </div>

      <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
        <el-table :data="follows" class="w100">
          <el-table-column prop="customerId" label="跟进人" />
          <el-table-column prop="gmtCreateTime" label="跟进时间" >
            <template slot-scope="scope">{{scope.row.gmtCreateTime | solveTime}}</template>
          </el-table-column>
          <el-table-column prop="result" label="跟进结果" />
        </el-table>
        <span slot="footer">
          <el-button @click="e=>dialogFormVisible=false">取消</el-button>
        </span>
      </el-dialog>
      <AddFollow :dialogFollowVisible="dialogFollowVisible" :parentId="parentId" @callback="addFollowCb"/>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import MemberPagination from '@/components/memberPagination'
  import AddFollow from '@/components/addFollow'
  import {Message} from "element-ui";

  export default {
    name: 'Customer',
    data() {
      return {
        queryForm: {
          phone: '',
          customerName: '',
          flowerName:''
        },
        queryFormCopy:{},
        tableHeaderBtnGroups: [
          {title:'创建', type: '1', children: [{title: '客户', name: 'Customer'},{title: '跟进记录', name: 'Follow'}]},
          {title:'转交', type: '2', children: [{title: '负责人', name: 'Director'},{title: '公共客户', name: 'Public'}]},
          {title:'导出', type: '3', children: [{title: '所选客户', name: 'SelectedCustomer'},{title: '当前页客户', name: 'CurrentCustomer'}]}
        ],
        customers: [],
        dialogFormVisible: false,
        dialogTitle: '',
        follows:[],
        dialogFollowVisible: false,
        selectedData:[],
        pages: {
          pageNum: 1,
          pageSize: 10
        },
        total: null,
        parentId:null
      }
    },
    computed: {
      flowerId() {
        let _this = this
        let name = _this.$route.name
        return name == 'TotalCustomer' ? 0 : name == 'PersonalCustomer' ? 1 : name == 'PublicCustomer' ? -1 : null
      }
    },
    components: {MemberPagination, AddFollow},
    mounted() {
      let _this = this
      _this.init()
    },
    methods: {
      init() {
        let _this = this
        _this.queryContent('query', _this.pages)
      },
      queryContent(type, _pages, dimension=0) {
        let _this = this
        let {customerList} = Apis.member
        let _followId = _this.flowerId
        let params = type == 'query' ? {..._this.queryForm, flowerId: _followId, type: dimension} : type == 'pagination' ? {..._this.queryFormCopy, flowerId: _followId, type: dimension}: {}
        params = _pages ? {...params, ..._pages} : {...params, ..._this.pages}
        customerList(params).then(res => {
          if (res.data.status == 200) {
            let {records, total} = res.data.data || {}
            _this.total = total
            _this.customers = records
          } else _this.$message.error(res.data.msg)
        })
      },
      reset() {
        let _this = this
        _this.$refs['queryForm'].resetFields()
      },
      lookRelaRems(obj) {
        let _this = this
        let {customerName, id} = obj
        _this.dialogFormVisible = true
        _this.dialogTitle = `客户${customerName}的跟进记录`
        let {flowerCustomerList} = Apis.member
        flowerCustomerList({customerId:id}).then(res => {
          if (res.data.status == 200) {
            _this.follows = res.data.data
          } else Message.error(res.data.msg)
        })
      },
      opera(operaType, detail) {
        let _this = this
        let {name} = detail
        if (operaType == '1') {
          if (name == 'Follow') {
            _this.dialogFollowVisible = true
          } else if(name == 'Customer') {
            _this.$router.push({path: '/entercustomer'})
          }
        } else if(operaType == '2') {
          if (!_this.selectedData.length) {
            Message.warning('请选择数据')
            return false
          }
          let {changeCustomer} = Apis.member
          let customerId = []
          let staffId = []
          _this.selectedData.forEach(s => {
            customerId.push(s.id)
            staffId.push(s.flowerId)
          })
          let tx = {customerId,staffId}
          changeCustomer(tx).then(res => {
            if (res.data.status == 200) {
              _this.queryContent('query')
            } else Message.error(res.data.msg)
          })
          // if (name == 'Director') {
          //
          // } else if(name == 'Public') {
          //
          // }
        } else if(operaType == '3') {
          let { exportCustomer } = Apis.member
          let customerIds = []
          if (name == 'SelectedCustomer') {
            if (!_this.selectedData.length) {
              Message.warning('请选择数据')
              return false
            }
            _this.selectedData.forEach(s => {
              customerIds.push(s.id)
            })
          } else if(name == 'CurrentCustomer') {
            _this.customers.forEach(s => {
              customerIds.push(s.id)
            })
          }
          exportCustomer({customerIds}, {type: 'export'}).then(res => {
            if (res.status == 200) {
              const content = res.data
              const blob = new Blob([content])
              const fileName = `${new Date().getTime()}_导出结果.xlsx`
              if ('download' in document.createElement('a')) {
                const elink = document.createElement('a')
                elink.download = fileName
                elink.style.display = 'none'
                elink.href = URL.createObjectURL(blob)
                document.body.appendChild(elink)
                elink.click()
                URL.revokeObjectURL(elink.href)
                document.body.removeChild(elink)
              } else {
                navigator.msSaveBlob(blob, fileName)
              }
            } else Message.error(res.data.msg)
          })

        }
      },
      addFollowRember(obj) {
        let _this = this
        let {id} = obj
        _this.parentId = id
        _this.dialogFollowVisible = true
      },
      addFollowCb(isOpen) {
        let _this = this
        _this.dialogFollowVisible = isOpen
      }
    }
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-btn-group {
      text-align: right;
    }
    .table-header{
      margin: 30px 0 15px;
      .table-header-btn-group{
        text-align: right;
        .dropdown:last-child{
          margin-right: 0;
        }
      }
    }
    .hover-active{
      color: #3a8ee6;
    }
  }
</style>
